import React from 'react'
import "./SearchBarStyle.css"
import api from '../api.js';
import { useDispatch, useSelector } from 'react-redux';
import { setIsSearchPropertiesLoading, setSearchPropertiesErrorMessage, setSearchResultsList, setSearchString, setSelectedProperty, setShowResults, setUniquePropertyValues, setUniqueValuesErrorMessage } from '../state/propertiesSearch/propertiesSearchSlice.js';

function SearchBar() {
    const dispatch = useDispatch()
    const searchStringValue = useSelector((state)=>state.propertiesSearch.searchString);
    async function onChange(value){
      try{
        dispatch(setIsSearchPropertiesLoading(true));
        dispatch(setSearchPropertiesErrorMessage(""));
        dispatch(setSearchResultsList([]));
        dispatch(setSearchString(value));
        dispatch(setShowResults(true));
        dispatch(setSelectedProperty(""));
        dispatch(setUniquePropertyValues([]));
        dispatch(setUniqueValuesErrorMessage(""));

        const res = await api.post("search/search_properties_by_name",
            {
                "property_name": value
            }
        );

        dispatch(setSearchResultsList(res.data["property_objects"]))
        dispatch(setIsSearchPropertiesLoading(false));


      }catch(e){
        dispatch(setSearchPropertiesErrorMessage(String(e)));
        dispatch(setIsSearchPropertiesLoading(false));
      }
    }
  return (
    <div className='search-bar-container'>
        <input onChange={e=>onChange(e.target.value)} value={searchStringValue} className='search-propeerty-input' placeholder='Search for Property'></input>
    </div>
  )
}

export default SearchBar