import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    searchString : "",
    isSearchPropertiesLoading: false,
    searchResultsList: [],
    searchPropertiesErrorMessage: "",
    selectedSubCategory: "",
    selectedProperty: "",
    showResults: true,
    uniquePropertyValues: [],
    uniqueValuesLoading: false,
    uniqueValuesErrorMessage: ""

}

const propertiesSearchSlice = createSlice({
    name: "propertiesSearch",
    initialState,
    reducers:{
        setSearchString: (state, action)=>{
            state.searchString = action.payload;
        },
        setIsSearchPropertiesLoading: (state, action)=>{
            state.isSearchPropertiesLoading = action.payload;
        },
        setSearchResultsList: (state, action)=>{
            state.searchResultsList = action.payload;
        },
        setSearchPropertiesErrorMessage: (state, action)=>{
            state.searchPropertiesErrorMessage = action.payload;
        },
        setSelectedSubCategory: (state, action)=>{
            state.selectedSubCategory = action.payload;
        },
        setSelectedProperty: (state, action)=>{
            state.selectedProperty = action.payload;
        },
        setShowResults: (state, action)=>{
            state.showResults = action.payload;
        },
        setUniquePropertyValues: (state, action)=>{
            state.uniquePropertyValues = action.payload;
        },
        setUniqueValuesLoading: (state, action)=>{
            state.uniqueValuesLoading = action.payload;
        },
        setUniqueValuesErrorMessage: (state, action)=>{
            state.uniqueValuesErrorMessage = action.payload;
        }
    }
});

export const {setSearchString, setIsSearchPropertiesLoading,setSearchResultsList, setSearchPropertiesErrorMessage, setSelectedSubCategory, setSelectedProperty, setShowResults, setUniquePropertyValues, setUniqueValuesLoading, setUniqueValuesErrorMessage} =  propertiesSearchSlice.actions;
export default propertiesSearchSlice.reducer;

