import React from 'react'
import "./SearchResultStyle.css"
import api from '../api.js';
import { useDispatch } from 'react-redux'
import { setSearchString, setSelectedProperty, setShowResults, setUniquePropertyValues, setUniqueValuesErrorMessage, setUniqueValuesLoading } from '../state/propertiesSearch/propertiesSearchSlice'

function SearchResult(props) {
  const dispatch = useDispatch()
  async function onClickTile() {
    try{
      dispatch(setShowResults(false));
      dispatch(setSelectedProperty(props.searchResult.name));
      dispatch(setUniqueValuesLoading(true));
      dispatch(setUniqueValuesErrorMessage(""));
      dispatch(setSearchString(props.searchResult.name));

   const res = await api.post("search/get_unique_values",
    {
    "property": props.searchResult.name
   }
  );


  dispatch(setUniqueValuesLoading(false));
  if(res.data["unique_values"].length == 0){
    throw new Error("Property name was not found");

  }

  var arr_res = [];
  for(let  val of res.data["unique_values"]){
    arr_res.push({value: val, label: val})
  }
  dispatch(setUniquePropertyValues(arr_res))
  // console.log(res.data["unique_values"]);
    }catch(e){
      dispatch(setUniqueValuesErrorMessage(String(e)));
      dispatch(setUniqueValuesLoading(false));

    }
  }
  return (
    <div onClick={onClickTile} className='property-search-result-item'>
     {props.searchResult.name}
    </div>
  )
}

export default SearchResult