import React from 'react'
import "./SearchResultListStyle.css"
import { useSelector } from 'react-redux'
import SearchResult from './SearchResult'
function SearchResultList() {
  const searchResults = useSelector((state)=>state.propertiesSearch.searchResultsList)
  return (
    <div className='property-search-result-list'>{
      searchResults.map((searchResult, id)=>{
        return <SearchResult key = {id}  searchResult = {searchResult}/>
      })
    
    }</div>
  )
}

export default SearchResultList