import React from 'react'
import "./SubCategoryTileStyle.css"
import { useDispatch } from 'react-redux';
import { setSelectedSubCategory } from '../state/propertiesSearch/propertiesSearchSlice';
import { useNavigate } from 'react-router-dom';
function SubCategoryTile(props) {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  function handleClick(){
    dispatch(setSelectedSubCategory(props.title));
    navigate('/search');

  }

  return (
   <div onClick = {handleClick} className='sub-category-card'>
<img src = {props.imgSrc}></img>
<h3>{props.title}</h3>
 </div>
  )
}

export default SubCategoryTile